@use "../variables";
@use "../mixins";

.hero {
  position: relative;
  padding: 51px 0;
  color: variables.$color-default-white;
  background-image: conic-gradient(from 130.31deg at calc(50% - 465px) 257px, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);

  @include mixins.vp-tablet {
    padding: 82px 0;
    background-image: conic-gradient(from 139.4deg at 160px 198px, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }

  @include mixins.vp-mobile {
    padding: 80px 0;
    background-image: conic-gradient(from 171.43deg at 113px 168px, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}

.hero .hero__container {
  display: flex;
}

.hero p {
  max-width: 113px;
  margin: 306px 18px 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: right;

  @include mixins.vp-tablet {
    margin: 188px 23px 0 0;
  }

  @include mixins.vp-mobile {
    position: absolute;
    margin: 155px 0 0 0;
  }
}

.hero__content {
  margin-top: 121px;
  margin-right: 64px;
  max-width: 247px;

  @include mixins.vp-tablet {
    margin-top: 48px;
    margin-right: 30px;
    max-width: 188px;
  }

  @include mixins.vp-mobile {
    margin: 23px 0 0 auto;
    min-width: 100%;
  }
}

.hero h5 {
  margin-bottom: 25px;
  margin-left: 63px;
  font-size: 48px;
  font-family: variables.$ff-primary;
  line-height: 37px;
  font-weight: 600;
  text-align: center;
  text-transform: lowercase;
  letter-spacing: variables.$letter-space;

  @include mixins.vp-tablet {
    margin-bottom: 15px;
    font-size: 34px;
    text-align: right;
  }

  @include mixins.vp-mobile {
    margin-top: 5px;
    margin-right: 4px;
    margin-bottom: 15px;
    font-size: 28px;
    line-height: 33px;
  }
}

.hero h1 {
  margin-bottom: 52px;
  font-size: 92px;
  font-family: variables.$ff-primary;
  line-height: 110px;
  font-weight: 700;
  text-align: right;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  -webkit-text-stroke: 2px variables.$color-default-white;

  @include mixins.vp-tablet {
    margin-bottom: 35px;
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
  }

  @include mixins.vp-mobile {
    margin-bottom: 25px;
  }
}

.hero img {
  @include mixins.vp-tablet {
    width: 324px;
    height: 390px;
    object-fit: cover;
  }

  @include mixins.vp-mobile {
    display: none;
  }
}

.hero__btn {
  width: 93%;
  margin-left: auto;
  padding: 23px 28px 20px 24px;

  @include mixins.vp-tablet {
    width: 97%;
    padding: 23px 28px 22px 24px;
  }

  @include mixins.vp-mobile {
    width: 100%;
    padding: 23px;
  }
}
