@use "../variables";
@use "../mixins";

.advantages {
  padding: 108px 0 123px;
  background-color: variables.$color-additional-background;

  @include mixins.vp-tablet {
    padding: 91px 0 93px;
  }

  @include mixins.vp-mobile {
    padding: 64px 0;
  }
}

.advantages .advantages__container {
  @media (min-width: 1366px) {
    padding: 0 160px;
  }
}

.advantages h2 {
  margin-bottom: 43px;
  margin-left: 8px;
  font-size: 54px;
  font-family: variables.$ff-primary;
  font-weight: 600;
  line-height: 65px;
  text-align: center;
  letter-spacing: variables.$letter-space;

  @include mixins.vp-tablet {
    margin-bottom: 42px;
    margin-left: 0;
    font-size: 42px;
    line-height: 50px;
  }

  @include mixins.vp-mobile {
    margin-bottom: 43px;
    font-size: 32px;
    line-height: 38px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 340px 240px 340px;
  grid-template-rows: 1fr 1fr;
  gap: 148px 61px;
  margin: 0;
  padding: 0;
  list-style: none;

  @include mixins.vp-tablet {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content min-content min-content;
    gap: 32px;
  }

  @include mixins.vp-mobile {
    display: flex;
    flex-direction: column;
    gap: 33px;
  }
}

.advantages__item {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 30px;

  @include mixins.vp-tablet {
    column-gap: 29px;
  }

  @include mixins.vp-mobile {
    padding: 0 15px;
    display: block;
    text-align: center;
  }
}

.advantages__item--first {
  position: relative;
  margin-left: 19px;

  @include mixins.line-advantages;

  @include mixins.vp-tablet {
    margin-left: 0;

    &::after {
      height: 0;
    }
  }
}

.advantages__item--second {
  position: relative;

  @include mixins.line-advantages;

  @include mixins.vp-tablet {
    &::after {
      height: 0;
    }
  }
}

.advantages__item--fourth {
  margin-left: 19px;

  @include mixins.vp-tablet {
    margin-left: 0;
  }
}

.advantages__item--fifth {
  grid-column: 3/4;
  grid-row: 2/2;

  @include mixins.vp-tablet {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}

.advantages h4 span {
  display: block;
  margin-top: -4px;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: #2e35ac;

  @include mixins.vp-mobile {
    margin-bottom: 10px;
  }
}


.advantages p {
  line-height: 23px;
}

.advantages h4 {
  grid-row: 1/3;
  font-size: 71px;
  font-weight: 700;
  line-height: 1;
  color: transparent;
  background: linear-gradient(127.61deg, #d10a42 0, #0041ca 65.58%);
  background-clip: text;
  width: min-content;

  @include mixins.vp-tablet {
    font-size: 58px;
    line-height: 58px;
  }

  @include mixins.vp-mobile {
    margin: 0 auto -4px;
  }
}

.advantages h3 {
  margin-bottom: 1px;
  font-size: 22px;
  font-weight: 700;
  line-height: 41px;

  @include mixins.vp-tablet {
    font-size: 20px;
    line-height: 30px;
  }
}

.advantages__item--third {
  display: block;
  margin-top: 11px;
  margin-bottom: 6px;
  grid-row: 1/3;
  grid-column: 2/3;
  padding: 49px 34px 59px;
  text-align: center;
  color: variables.$color-default-white;
  background-color: #1c3374;

  @include mixins.vp-tablet {
    width: 442px;
    margin: 11px auto 10px;
    grid-row: 2/2;
    grid-column: 1/3;
    padding: 30px 34px 40px;
  }

  @include mixins.vp-mobile {
    width: calc(100% + 30px);
    margin: 11px -15px 10px;
    padding: 42px 34px 44px;
  }
}

.advantages__item--third h3 span {
  display: block;
  margin-bottom: 36px;
  font-size: 26px;
  line-height: 34px;

  @include mixins.vp-tablet {
    margin-bottom: 12px;
    font-size: 22px;
  }

  @include mixins.vp-mobile {
    margin-bottom: 8px;
  }
}

.advantages__item--third h3 {
  font-size: 74px;
  line-height: 83px;
  color: variables.$color-default-white;

  @include mixins.vp-tablet {
    display: block;
    margin-bottom: 7px;
    line-height: 78px;
  }

  @include mixins.vp-mobile {
    line-height: 64px;
  }
}

.advantages__item--third p {
  font-weight: 400;
}
